@import "main-bx";

.menu__submenu {
    position: absolute;
    top: 80px;
    display: none;
    transition: all .3s linear;
    @include below(1040px) {
        position: sticky;
        top: auto;
        display: block;
        max-height: 0;
        overflow: hidden;
    }

    &.height {        
        @include below(1040px) {
            max-height: 50rem;
        }
    }

    li {
        font-family: $ubuntu_font;
        font-weight: 700;
        font-size: 15px;
        line-height: 32px;
        letter-spacing: 0.5px;
        margin: 15px 0;

        @include below(1040px) {
            font-weight: 400;
            font-size: 14px;
        }

        a {
            color: $white;
            transition: all .2s linear;
            padding: 15px 0;
            &:hover {
                opacity: 0.7;
            }
        }
    }

    &-more {
        right: 0;
        padding: 30px;
        width: 300px;
        background: rgba(12, 40, 114, 0.9);
        backdrop-filter: blur(24px);

        .menu__submenu {
            position: static;
        }
    }
}