@import "main-bx";

.navigation {
    padding: 54px 0 30px;

    &__img-mob {
        display: none;
    }

    &__top {
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        margin-bottom: 48px;
        &-icon {
            width: 100%;
            max-width: 80px;
            height: 80px;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: $blue;
            border-radius: 15px;
            margin-right: 40px;
            img {
                width: 44px;
                height: 45px;
            }
        }

        &-right {
            width: 57%;
            display: flex;
        }

        p {
            font-size: 21px;
            line-height: 140%;
            letter-spacing: 0.005em;
            margin-top: -4px;
        }
    }
    &__bottom {
        background: #F5F8FB;
    }
    &__nav {
        padding: 44px 0 33px;
        
        &-list {
            display: flex;
            flex-wrap: wrap;
            margin-top: -10px;
            margin-left: -10px;
        }

        &-item {
            flex: auto;
            margin-left: 10px;
            margin-top: 10px;    
            background: $white;
            border-radius: 15px;
            &:hover {
                box-shadow: 0px 5px 10px rgba(210, 224, 238, 0.3);
            }
            &:active {
                box-shadow: 0px 5px 10px rgba(210, 224, 238, 0);
            }
        }
        
        &-link {
            display: flex;
            align-items: center;
            padding: 17px;
            color: $deep_dark_blue;
            transition: all .2s linear;
            border-radius: 15px;
            &.active {
                background-color: $blue;
                color: $white;
            
                .navigation__nav-circle {
                    background-color: $white;
                }
            }
        }

        &-circle {
            display: inline-block;
            width: 16px;
            height: 16px;
            border-radius: 50%;
            background-color: $grey;
            margin-right: 10px;
            transition: all .2s linear;
        }
    }
}

@include below(1441px) {
    .navigation {
        &__top {
            &-right {
                width: 54%;
            }

            p {
                font-size: 19px;
            }
        }
    }
}

@include below(769px) {
    .navigation {
        padding: 25px 0;

        &__img-mob {
            display: block;
        }

        &__top {
            margin-bottom: 25px;
            &-right {
                width: 100%;
            } 

            &-icon {
                width: 40px;
                height: 30px;
                border-radius: 3px;
                margin-right: 12px;
                img {
                    width: 15px;
                    height: 15px;
                }
            }

            p {
                font-size: 12px;
            line-height: 140%;
            }
        }

        &__title,
        &__bottom {
            display: none;
        }
    }
}