@import "main-bx";

.in-number {
    padding: 30px 0;

    &__title {
        text-align: center;
        margin-bottom: 50px;
    }

    &__cards {
        display: flex;
        flex-wrap: wrap;
        margin-top: -27px;
        margin-left: -27px;
        
        &-top {
            margin-bottom: 45px;
        }

        &-item {
            width: 22.8%;
            display: flex;
            flex-direction: column;
            padding: 29px 20px 31px 36px;
            border-radius: 15px;
            background-color: $smoke_blue;
            margin-top: 27px;
            margin-left: 27px;

            &:last-child {
                .in-number__cards-numb {
                    font-size: 24px;
                    line-height: 100%;
                }
            }
        }

        &-numb {
            color: $blue2;

            @include below(370px) {
                font-size: 25px;
            }
        }

        &-subtitle {
            color: $deep_dark_blue3;
        }
    }
}

@include below(1281px) {
    .in-number {
        &__cards {
            &-item {
                width: 30.7%;

                &:last-child {
                    .in-number__cards-top {
                        margin-bottom: 70px;
                    }
                }
            }
        }
    }
}

@include below(map-get($breaks, lg)) {
    .in-number {
        &__cards {
            margin: 0;
            margin-top: -20px;
            justify-content: space-between;
            &-item {
                margin: 0;
                margin-top: 20px;
                width: 49%;
            }
        }
    }
}

@include below(769px) {
    .in-number {

        padding: 25px 0;

        &__title {
            margin-bottom: 37px;
        }

        &__cards {

            &-top {
                display: flex;
                margin-bottom: 25px;
            }

            &-subtitle {
                align-self: flex-end;
                margin-top: -5px;
                margin-left: 7px;
                font-size: 10px;
                line-height: 106%;
                color: #2F323D;
            }

            &-item {
                height: auto;
                padding: 15px;
                border-radius: 9px;
                width: 47.5%;
                margin-top: 20px;
                &:nth-child(1) {
                    order: 1;
                }
                &:nth-child(2) {
                    order: 2;
                }
                &:nth-child(5) {
                    order: 3;
                } 
                &:nth-child(6) {
                    order: 4;
                }
                &:nth-child(3) {
                    order: 5;
                    .in-number__cards-top {
                        flex-direction: column;
                    }
                    .in-number__cards-subtitle {
                        align-self: flex-start;
                        margin: 0;
                    }
                }
                &:nth-child(4) {
                    order: 6;
                }
                &:nth-child(7) {
                    order: 7;
                }
                &:last-child {
                    
                    order: 8;

                    .in-number__cards-numb {
                        font-size: 14px;
                        line-height: 100%;
                    }
                    .in-number__cards-top {
                        flex-direction: column;
                        margin-bottom: 35px;
                    }
                    .in-number__cards-subtitle {
                        align-self: flex-start;
                        margin: 0;
                    }
                }
            
            }

            &-numb {
                font-size: 30px;

            }    
        }
    }
}
