@import "main-bx";

.premise {
    padding: 30px 0;
    @include below(769px) {
        padding: 30px 0 0;
        overflow: hidden;
    }
    &__wrap {
        position: relative;
    }

    &__img {
        @include below(769px) {
            width: 118%;
            margin-left: -10%;
            height: 325px;
        }
    }

    &__nav {
        @include below(769px) {
            display: flex;
            left: 0;
            width: 100%;
            z-index: 100;
            top: 45%;
        }
    }

    &__title {
        padding-bottom: 45px;
        text-align: center;

        @include below(769px) {
            padding-bottom: 30px;
        }
    }

    &__inner {
        position: relative;
        z-index: 10;
        overflow: hidden;
        border-radius: 25px;
        height: 100%;
        .slick-list,
        .slick-track {
            height: 100%;
        }

        @include below(769px) {
            overflow: visible;
        }
    }

    &__item {
        position: relative;
    
        &:not(:first-child) {
            display: none;
        }
       
        &.slick-slide {
            display: block;
        }
    }

    &__slider {
        .slick-list {
            @include below(769px) {
                overflow: visible !important;
            }
        }
    }

    &__card {
        position: absolute;
        left: 38px;
        bottom: 33px;
        border-radius: 15px;
        width: 100%;
        max-width: 580px;
        overflow: hidden;

        @include below(769px) {
            max-width: 100%;
            position: sticky;
        }

        &-head {
            display: flex;
            align-items: center;
            padding: 17px 33px;
            background-color: $blue2;

            @include below(769px) {
                padding: 13px 20px;
            }
        }

        &-icon {
            margin-right: 30px;

            img {
                width: 35px;
                height: 35px;

                @include below(769px) {
                    width: 20px;
                    height: 20px;
                }
            }
        }

        &-numb {
            font-weight: 700;
            font-size: 25px;
            line-height: 115%;
            text-transform: uppercase;
            color: $white;

            @include below(769px) {
                font-size: 16px;
                line-height: 115%;
            }

            @include below(370px) {
                font-size: 12px;
            }
        }

        &-body {
            padding: 26px 33px 33px;
            background-color: $smoke_blue;

            @include below(769px) {
                filter: drop-shadow(0px 0px 12.8186px rgba(0, 0, 0, 0.2));
                padding: 20px;
            }

            p {
                font-size: 12px;
                line-height: 140%;

                @include below(370px) {
                    font-size: 10px;
                }
            }
        }
    }
}