@import "main-bx";

.graph__nav {
    position: relative;
    display: flex;
    justify-content: space-between;
    z-index: 100;
    .slick-list {
        z-index: 10;
    }
    &-item {
        width: 100px;
        position: relative;
        z-index: 1;
        text-align: center;
        cursor: pointer;
       
        padding-bottom: 10px;
        &:nth-child(n + 6) {
            display: none;
        }

        &.active {
            .graph__nav-year {
                opacity: 1;
            }
            .graph__nav-btn {
                transform: scale(1.3);
                border: 3px solid #FFFFFF;
                background: #3C4CFD;

                @include below(769px) {
                    transform: scale(1.2);
                }
            }
        }
    
    }
    &-year {
        font-weight: bold;
        font-size: 14px;
        line-height: 23px;
        color: #2F323D;
        opacity: 0.5;
        margin-bottom: 30px;
        transition: all .25s linear;

        @include below(769px) {
            font-size: 10px;
            line-height: 18px;
            margin-bottom: 5px;
        }
    }

    &-btn {
        position: sticky;
        z-index: 10;
        width: 22px;
        height: 22px;
        border-radius: 50%;
        background: #FFFFFF;
        border: 3px solid #BCD6EF;
        cursor: pointer;
        transition: all .25s linear;

        @include below(769px) {
            width: 19px;
            height: 19px;
        }
    }
}