@import "main-bx";

.constructions__figures {
    display: flex;
    flex-wrap: wrap;
    padding: 33px 60px 26px;
    background-color: $blue3;
    color: $white;

    @include below(769px) {
        padding: 27px 30px;
    }

    &-title {
        width: 20%;
        flex: 1 0 auto;
        font-weight: 500;
        font-size: 30px;
        line-height: 115%;
        
        letter-spacing: 0.5px;
        
        @include below(1100px) {
            width: 100%;
            margin-bottom: 20px;
        } 

        @include below(769px) {
            display: none;
        }

        &_mob {
            display: none;

            @include below(769px) {
                display: block;
                font-size: 16px;
                line-height: 115%;
                margin-bottom: 0;
                margin-top: 25px;
                color: #2838E9;
            }
        }
    }

    &-list {
        width: 80%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-top: -20px;
        @include below(1100px) {
            width: 100%;
        }
    }

    &-item {
        width: 23%;
        margin-top: 20px;
        
        @include below(769px) {
            width: 40%;
        }
    }

    &-top {
        display: block;
        margin-bottom: 10px;
    }

    &-numb {
        font-weight: bold;
        font-size: 40px;
        line-height: 115%;
        margin-right: 7px;

        @include below(769px) {
            font-size: 25px;
        }
    }

    &-unit {
        font-weight: 500;
        font-size: 17px;
        line-height: 106%;

        @include below(769px) {
            font-size: 12px;
        }
    }

    &-text {
        font-weight: normal;
        font-size: 14px;
        line-height: 130%;

        @include below(769px) {
            font-size: 10px;
            line-height: 125%;
        }
    }
}

@include below(map-get($breaks, lg)) {
    .constructions__figures {
        flex-wrap: wrap;
        &-title {
            margin-bottom: 30px;
        }
    }
}