@import "main-bx";

.history {
    background-color: $smoke_blue;
    overflow: hidden;
    position: relative;
    &__img-mob {
        position: absolute;
        bottom: 30px;
        left: 0;
        display: none;

        @include below(400px) {
            top: 130px;
        }

        @include below(350px) {
            height: 250px;
        }
    }

    &__top {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        padding-top: 53px;
        position: relative;
    }

    &__btn-nav {
        position: absolute;
        top: -3px;
        left: 0;
        right: 0;
        width: 100%;
        z-index: 10;
        @include below(769px) {
            display: flex;
        }

        @include below(400px) {
            top: 40px;
        }
    }

    &__title {
        margin-right: 70px;
    }

    &__content {
        display: flex;
        width: 100%;
        max-width: 1200px;
        @include below(1024px) {
            width: auto;
            max-width: 600px;
        }

        @include below(350px) {
            padding: 15px 0;
        }
        .slick-arrow {
            display: none !important;
        } 

        &-item {
            margin-right: 60px;
            width: 500px;
            &:last-child {
                width: 640px;
            }
            &:not(:first-child) {
                display: none;
            }

            &.slick-slide {
                display: block;
            }
        }
        
        &-inner {
            display: flex;
        }

        &-img {
            padding-right: 17px;
            width: 100%;
            max-width: 56px;
            margin-right: 25px;
            border-right: 3px solid $white;
            
            img {
                height: auto;
            }
        }

        &-text {
            font-weight: normal;
            font-size: 12px;
            line-height: 160%;
            color: #2F323D;
            padding-bottom: 50px;
        }
    }

    &__bottom {
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative;
        padding: 17px 0 12px;
        padding-left: 120px;

        @include below(350px) {
            padding: 0;
        }

        &_mobile {
            display: none;
        }
        @include below(1024px) {
            padding-left: 20px;
        }
        &-tape {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            width: 110%;
            z-index: 1;

            @include below(400px) {
                width: 120%;
            }
        }
    }

    &__year {
        position: sticky;
        z-index: 10;
        width: 100%;
        max-width: 1200px;
        margin-right: -200px;
        color: $white;
        

        @include below(1024px) {
            width: auto;
            max-width: 550px;
        }

        
        
        &-item {
            width: 600px;
            &:not(:first-child) {
                display: none;
            }

            &.slick-slide {
                display: block;
            }
        }

        p {
            font-size: 30px;
            line-height: 115%;
        }
    }

    
}

@include below(1281px) {
    .history {
        &__year {
            max-width: 700px;
            margin-right: 0;
            &-item {
                width: 500px;
            }
        }

        &__bottom {
            &-tape {
                height: 100%;
            }
        }
    }
}

@include below(769px) {
    .history {
        padding: 25px 0;
        background: transparent;

        &__img-mob {
            top: 70px;
            width: 100%;
            bottom: auto;
            display: block;
        }

        &__top {
            padding-top: 0;
            flex-direction: column;
        }

        &__title {
            width: 100%;
            text-align: center;
            margin-bottom: 30px;
            margin-right: 0;
            br {
                display: none;
            }
        }

        &__content {
            max-width: 100%;
            padding: 15px 0 15px 22px;

            &-item {
                margin-right: 20px;
            }

            &-text {
                padding-bottom: 0;
            }
            &-img {
                display: none;
            }
        }

        &__bottom {
            display: none;
            &_mobile {
                width: 100%;
                padding: 20px;
                display: flex;
                height: 60px;
            }

            &-tape {
                width: 110%;
                right: auto;
                left: -30px;
            }
        }

        &__year {
            max-width: 100%;

            .slick-arrow {
                display: none !important;
            }
            .slick-track {
                display: flex;
                align-items: center;
            }
            p {
                font-size: 12px;
                line-height: 115%;
                text-decoration-line: underline;
                color: #F5F8FB;
                opacity: 0.5;
                transition: all .2s linear;
            }

            .slick-current p {
                font-weight: 700px;
                font-size: 16px;
                line-height: 115%;
                text-decoration-line: none;
                color: #fff;
                opacity: 1;
            }

            &-item {
                width: auto;
                margin-right: 27px;
            }
        }
    }
}


@media (max-width: 500px) {
    .history__img-mob {
        top: 135px;
        height: 400px;
    }
}