@import "main-bx";

.social {
    &:not(:last-child) {
        margin-right: 10px;
    }
    a {
        &:hover svg {
            transform: scale(1.2);
        }
        &:active svg {
            transform: scale(1);
        }
    }
    svg {
        width: 20px;
        height: 20px;
        transition: all .25s linear;
    }
}

