@import "main-bx";

.intro {
    position: relative;
    &__content {
        position: absolute;
        top: 170px;
        right: 0;
        left: 0;
    }

    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        position: relative;
        font-family: $rubik_font;
        color: $white;

        &-item {
            width: 50%;
        }
    }

    &__img {
        img {
            object-fit: contain;
        }
    }

    &__frame {
        display: block;
        width: 100%;
        max-width: 622px;
        height: 573px;
        border-radius: 17px;
        overflow: hidden;
    }

    &__title {
        font-weight: 700;
        font-size: 55px;
        line-height: 115%;
        letter-spacing: 0.005em;
        margin-bottom: 24px;
    }

    &__text {
        font-weight: 400;
        font-size: 18px;
        line-height: 130%; 
        letter-spacing: 0.5px;
        margin-bottom: 50px;
        padding-right: 200px;
    }
    &__link {
        display: block;
        width: 100%;
        max-width: 300px;
        padding: 19px 20px;
        text-align: center;
    }
}

@include below(1441px) {
    .intro {
        &__frame {
            max-width: 565px;
            height: 515px;
        }
    }
}

@include below(1300px) {
    .intro {
        &__frame {
            max-width: 565px;
            height: 415px;
        }

        &__inner {
            &-item {
                width: 49%;
            }
        }

        &__text {
            padding-right: 40px;
        }
    }
}

@include below(1124px) {
    .intro {
        &__frame {
            height: 304px;
        }
    }
}

@include below(930px) {
    .intro {
        &__frame {
            display: none;
        }
    }
}

@include below(769px) {
    .intro {
        
        &__inner {
            &-item {
                width: 100%;
                &:last-child {
                    display: none;
                }
            }
        }

        &__img {
            height: 535px;

            img {
                object-fit: cover;
            }
        }

        &__content {
            top: 125px;
        }

        &__title {
            font-size: 36px;
            line-height: 110%;
            margin-bottom: 24px;
        }

        &__text {
            font-size: 14px;
            line-height: 130%;
            margin-bottom: 37px;
            padding-right: 0;
        }
    }
}