@import "main-bx";

.peculiarities {
    padding: 40px 0 40px;

    &__container {
        position: relative;
    }

    &__nav {
        top: auto;
        bottom: 15%;
    }

    &__item {
        display: none;
        width: 25%;
        &:not(:last-child) {
            margin-right: 50px;
        }

        &:nth-child(1),
        &:nth-child(2),
        &:nth-child(3) {
            display: block;
        }
        
        &:nth-child(3) {
            @include below(1000px) {
                display: none;
            }
        }

    }

    &__top {
        display: flex;
        justify-content: space-between;
        margin-bottom: -20px;

        @include below(1281px) {
            margin-bottom: 40px;
        }
    }

    &__img {
        width: 50%;
        position: sticky;
        z-index: 10;
        transform: translateY(90px);

        &_mob {
            display: none;
        }
    }

    &__context {
        width: 44%;
        &_mob {
            display: none;
            margin-top: 30px;
        }
    }

    &__title {
        margin-bottom: 45px;

        &_mob {
            display: none;
            margin-bottom: 33px;
        }

        @include below(769px) {
            padding: 0 20px;
        }
    }
    &__text {
        font-weight: normal;
        font-size: 17px;
        line-height: 155%;
        color: #0D223D;
        &:not(:last-child) {
            margin-bottom: 30px;
        }
    }

    &__bottom {
        position: sticky;
        z-index: 20;
        padding: 75px 35px 45px 47px;
        border-radius: 0px 5px 5px 5px;
        background-color: $smoke_blue;
    }

    &__list {
        display: flex;
        justify-content: space-between;
        svg {
            width: 22.94px;
            height: 26px;
            margin-bottom: 22px;
            fill: $blue3;
        }

        p {
            font-size: 14px;
            line-height: 150%;
        }
    }

    
}

@include below(map-get($breaks, lg)) {
    .peculiarities {
        &__list {
            flex-wrap: wrap;
        }

        &__item {
            width: 50%;
            &:not(:last-child) {
                margin-right: 25px;
            }
        }
    }
}


@include below(769px) {
    .peculiarities {
        padding: 30px 0;
        
        &__item {
            width: 50%;
            margin-right: 25px !important;

            svg {
                width: 14.38px;
                height: 16.3px;
                margin-bottom: 14px;
            }
            
            p {
                font-size: 10px;
                line-height: 150%;
            }
        }

        &__nav {
            display: flex !important;
            top: -15px;
            width: 100%;
            left: 0;
            z-index: 100;
            padding: 0 20px;
        }

        &__top {
            display: none;
        }
        &__title {
            &_mob {
                text-align: center;
                display: block;
            }
        }

        &__img {
            &_mob {
                display: block;
                width: 100%;
                height: 300px;
                
                transform: translateY(3px);
            }
        }

        &__context {
            width: 100%;
            &_mob {
                display: block;
                padding: 0 20px;
            }
        }
    
        &__text {
            font-size: 12px;
            line-height: 140%;

            &:not(:last-child) {
                margin-bottom: 15px;
            }
        }

        &__bottom {
            padding: 40px 20px 35px;
        }

    }
}