@import "main-bx";

.agreement__params {
    padding-left: 100px;
    border-radius: 5px;
    background-color: $smoke_blue;
    padding: 60px 0;

    &-list {
        display: flex;
        margin-bottom: 50px;
        padding: 0 60px;
        padding-left: 350px;
        &-item {
            &:not(:first-child) {
                margin-left: 75px;

                @include below(370px) {
                    margin-left: 20px;
                }
            }
        }        
    }

    &-item {
        &:not(:last-child) {
            margin-bottom: 36px;
        }
    }

    &-key {
        font-weight: normal;
        font-size: 14px;
        line-height: 140%;

        color: #546577;
        margin-bottom: 10px;

        &::before {
            content: "";
            display: inline-block;
            width: 10px;
            height: 10px;
            border-radius: 50%;
            background: #ADC1D5;
            margin-right: 15px;
        }
    }

    &-val {
        font-weight: 500;
        font-size: 18px;
        line-height: 140%;
        color: #0C2872;
    }

    &-bottom {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        align-items: flex-start;
        border-top: 1px solid #ADC1D5;
        padding: 35px 60px 0;
        &-item {
            width: 50%;

            &:first-child span {
                margin-right: 10px;
            }

            &:last-child span {
                margin-right: 25px;
            }
        }

        p {
            display: flex;
        }

        span {
            font-weight: normal;
            font-size: 14px;
            line-height: 140%;
            color: #546577;
        }

        strong {
            font-weight: 500;
            font-size: 16px;
            line-height: 140%;
            color: #0B266C;
        }
    }
}

@include below(1281px) {
    .agreement__params {
        &-list {
            padding-left: 250px;
        }
        &-bottom {
            &-item {
                width: 100%;
                &:first-child {
                    margin-bottom: 20px;
                }
            }
        }
    }
}


@include below(1124px) {
    .agreement__params {
        &-list {
            padding: 0 60px;
            justify-content: space-between;
        }
    }
}

@include below(769px) {
    .agreement__params {
        background: #F5F8FB;
        padding: 40px 0 0;

        &-top {
            padding-bottom: 40px;
        }

        &-list {
            padding: 0 20px;
            margin-bottom: 40px;
        }

        &-key {
            font-size: 11px;
            line-height: 125%;
            margin-bottom: 10px;

            &::before {
                background: #3C4CFD;
                margin-right: 10px;
            }
        }

        &-val {
            font-size: 14px;
            line-height: 130%;
        }

        &-bottom {
            border-top: 1px dashed #ADC1D5;
            padding: 40px 20px;

            &-item {
               &:first-child {
                    margin-bottom: 30px;
               }
            }

            p {
                flex-direction: column;
            }

            p span {
                display: block;
                font-size: 11px;
                line-height: 140%;
                margin-bottom: 10px;
                br {
                    display: none;
                }
            }

            p span::before {
                content: "";
                display: inline-block;
                width: 10px;
                height: 10px;
                border-radius: 50%;
                background: #3C4CFD;
                margin-right: 10px;
            }

            p strong {
                font-size: 14px;
                line-height: 140%;
            }
        }
    }
}