@import "main-bx";

.agreements__obligation {
    padding: 45px;
    border-radius: 5px;
    background-color: $smoke_blue;
    &-list {
        display: flex;
        &:not(:last-child) {
            margin-bottom: 20px;
        }
        svg {
            width: 100%;
            max-width: 19.41px;
            height: 22px;
            fill: #ADC1D5;
            margin-right: 30px;
        }
    }
}

@include below(769px) {
    .agreements__obligation {
        padding: 24px;

        &-list {
            display: flex;
            &:not(:last-child) {
                margin-bottom: 12px;
            }

            svg {
                width: 100%;
                max-width: 19.41px;
                height: 13.77px;
            }
        }
    }
}