@import "main-bx";

.ecologic {
    padding: 40px 0 65px;
    &__inner {
        display: flex;
    }

    &__item {
        flex: 1 auto;
        
        &:first-child {
            padding-top: 30px;
            max-width: 465px;
            margin-right: 77px;
        }
    }

    &__title {
        margin-bottom: 60px;
        span {
            color: $green;
        }
    }

    &__text {
        font-weight: normal;
        font-size: 17px;
        line-height: 155%;
        &:last-child {
            margin-top: 30px;
        }

        &_mob {
            display: none;
        }
    }

    &__cards {
        position: relative;
        padding: 35px 38px 38px 37px;
        border-radius: 15px;
        overflow: hidden;
        &-img {
            position: absolute;
            top: 0;
            right: 0;
            left: 0;
            bottom: 0;
            z-index: 1;
        }

        &-wrap {
            position: sticky;
            z-index: 10;

            display: flex;
            flex-wrap: wrap;
            margin: -27px 0 0 -27px;
        }

        &-item {
            flex: 1 auto;
            flex-basis: 45%;
            max-width: 279px;
            padding: 23px 26px 30px 30px;
            
            background: rgba(255, 255, 255, 0.95);
            backdrop-filter: blur(24px);
            border-radius: 15px;

            margin: 27px 0 0 27px;
        }

        &-numb {
            font-weight: bold;
            font-size: 50px;
            line-height: 115%;
            color: $green;
        }
        
        &-text {
            margin-bottom: 50px;

            
        }

        &-text,
        &-subtext {
            font-weight: normal;
            font-size: 17px;
            line-height: 106%;

            @include below(370px) {
                font-size: 8px;
            }
        }
    }
}

@include below(1280px) {
    .ecologic {
        &__cards {
            padding: 20px;
            margin-bottom: 30px;

            &-wrap {
                margin: 0;
                margin-top: -15px;
                justify-content: space-between;
            }

            &-item {
                flex: none;
                width: 48.6%;
                max-width: 100%;
                padding: 13px 13px 18px 18px;
                margin: 0;
                margin-top: 15px;
            }

            &-numb {
                font-size: 29.5455px;
                line-height: 115%;
            }

            &-text {
                font-size: 10px;
                line-height: 106%;
                margin-bottom: 28px;
            }

            &-subtext {
                font-size: 10px;
                line-height: 106%;
            }
        }
    }
}

@include below(map-get($breaks, xl)) {
    .ecologic {
        &__inner {
            flex-wrap: wrap;
        }

        &__item {
            &:first-child {
                max-width: 100%;
                padding-top: 0;
                margin-right: 0;
                margin-bottom: 30px;
            }
        }
    }
}

@include below(769px) {
    .ecologic {
        padding: 30px 0 40px;

        &__container {
            padding: 0;
        }

        &__title {
            text-align: center;
            margin-bottom: 0;
        }

        &__text {
            display: none;
            padding: 0 20px;
            font-size: 12px;
            line-height: 140%;
            &:last-child {
                margin-top: 15px;
            }
            &_mob {
                display: block;
            }
        }
    }
}