@import "main-bx";

.effects {
    padding: 30px 0 80px;

    &__title {
        text-align: center;
        margin-bottom: 48px;
    }
    
    &__subtitle {
        font-weight: 500;
        font-size: 25px;
        line-height: 125%;
        color: $deep_dark_blue3;

        @include below(370px) {
            font-size: 10px;
        }
    }

    &__wrap {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: flex-end;
        margin-top: -20px;

        @include below(500px) {
            align-items: stretch;
        }
    }
    
    &__numb {
        font-weight: bold;
        font-size: 70px;
        line-height: 115%;
        color: $blue2;

        @include below(370px) {
            font-size: 27px;
        }
    }

    &__item {
        padding: 40px 50px;
        border-radius: 5px;
        display: flex;
        align-items: center;
        margin-top: 20px;
        &_first {
            width: 100%;
            max-width: 680px;
            background: #F8F8F8;
        }

        &_second {
            width: 100%;
            max-width: 500px;
            align-items: flex-end;
            background: #F5F8FB;
        }

        &_third {
            width: 100%;
            max-width: 587px;
            background: #F5F8FB;
            align-self: flex-start;
        }

        &_fourth {
            align-self: flex-start;

            @include below(500px) {
                align-self: stretch;
                align-items: flex-start;
            }
        }

        &_fifth {
            width: 100%;
            max-width: 300px;
            padding-bottom: 130px;
            background: #F8F8F8;
            flex-direction: column;

            .effects__left {
                display: flex;
                align-items: flex-end;
                margin-bottom: 10px;
            }

            .effects__subtitle {
                transform: translate(13px, -13px);
            }

            
        }


        &_last {
            width: 100%;
            max-width: 880px;
            background: #F5F8FB;
            margin-top: -140px;
            padding: 30px 45px;

            .effects__left {
                width: 50%;
                display: flex;
                align-items: flex-end;
            }
            .effects__subtitle {
                transform: translateY(-10px);
                margin-left: 10px;
            }
            .effects__text {
                width: 50%;
            }
        }
    }

    &__left {
        margin-right: 38px;
    }
}

@include below(1281px) {
    .effects {

        &__item {
            &_first {
                max-width: 565px;
            }
            &_second {
                max-width: 438px;
            }
            &_third {
                max-width: 565px;
            }
            &_fifth {
                max-width: 100%;
                padding-bottom: 40px;
                flex-direction: row;
                
            }
            &_last {
                max-width: 100%;
                margin-top: 20px;
            }
        }
    }
}


@include below(map-get($breaks, lg)) {
    .effects {

        &__item {
            &_first {
                max-width: 555px;
            }
            &_second {
                max-width: 372px;
                height: 215px;
            }
        }
    }
}

@include below(map-get($breaks, mlg)) {
    .effects {
        &__item {
            max-width: 100%;
        }
    }
}

@include below(769px) {
    .effects {
        padding: 25px 0;
        &__title {
            margin-bottom: 30px;
        }

        
        &__item {
            height: auto;
            padding: 20px 20px 25px;
            &:nth-child(2),
            &:nth-child(3),
            &:nth-child(4),
            &:nth-child(5) {
                width: 47.5%;
            }

            &:nth-child(1) {
                order: 1;
            }
            &:nth-child(2) {
                order: 4;
            }
            &:nth-child(3) {
                order: 2;
            }
            &:nth-child(4) {
                order: 3;
                background: rgba(248, 248, 248, .4);
            }
            &:nth-child(5) {
                order: 5;
            }
            &:nth-child(6) {
                order: 6;
            }

            &_third,
            &_second,
            &_fifth {
                flex-direction: column;
                align-items: flex-start;
                .effects__left {
                    display: flex;
                    align-items: flex-end;
                    margin-bottom: 10px;
                }

                .effects__numb {
                    margin-right: 12px;
                }

                .effects__subtitle {
                    transform: translate(0, 0);
                }
            }

            &_last {
                flex-direction: column;
                align-items: center;
                .effects__numb {
                    font-size: 32px;
                    line-height: 115%;
                    margin-bottom: 7px;
                }
                .effects__left {
                    width: 100%;
                }
                .effects__text {
                    width: 100%;
                }
            }
        }

        &__left {
            margin-right: 12px;
        }

        &__numb {
            font-size: 32px;
            line-height: 115%;
        }

        &__subtitle {
            font-size: 12px;
            line-height: 125%;
        }
    }
}