@import "main-bx";

.btn {
    cursor: pointer;
    display: flex;
    align-items: center;
    color: $white;
    transition: all .2s linear;
    padding: 0;

    font-weight: 700;
    font-size: 19px;
    line-height: 23px;

    @include below(769px) {
        font-size: 16px;
        line-height: 19px;
    }
    &_blue {
        background: #1421FF;
        border-radius: 15px;
        @media (max-width: 769px) {
            border-radius: 12px;
        }
        &:hover {
            background: #0069d9;
        }

        &:active {
            box-shadow: 0 0 0 0.2rem rgba(0,123,255, 0.5);
        }
    }
}
