@import "main-bx";

.menu {
    position: relative;
    display: flex;
    align-items: flex-start;

    @include below(1040px) {
        flex-direction: column;

        padding: 45px 20px;
    }

    &__item {
        transition: all .3s linear;
        &:not(:last-child) {
            margin-right: 50px;

            @include below(1040px) {
                margin-right: 0;
            }
        }

        @include below(1040px) {

            margin-bottom: 20px;
        }

        &-top {
            display: flex;
            align-items: center;
            cursor: pointer;

            font-family: $ubuntu_font;
            font-weight: 700;
            font-size: 15px;
            line-height: 32px;
            letter-spacing: 0.5px;
            @include below(1040px) {
                font-size: 18px;
            }
            span,
            a {
                color: $white;
                transition: all .2s linear;
                
                &:hover {
                    opacity: 0.7;
                }
            }

            svg {
                width: 10px;
                height: 6px;
                fill: $white;
                margin-left: 7px;
            }
        }
    }

    &__more {
        button {
            font-weight: bold;
            font-size: 15px;
            line-height: 32px;
            letter-spacing: 0.5px;
            color: #FFFFFF;
            cursor: pointer;
        }

        &-list {
            padding: 30px;
            position: absolute;
            top: 80px;
            right: 0;
            display: none;
            background: rgba(12, 40, 114, 0.9);
            backdrop-filter: blur(24px);
            li {
                margin: 15px 0;
            }

            .menu__submenu {
                position: static;
            }
        }
    }
}

