@import "main-bx";

.agreements {
    padding: 56px 0 62px;

    &__top {
        padding-left: 100px;
        position: relative;
        padding-bottom: 70px;
         
        &-title {
            height: 280px;
            padding: 47px;
            position: absolute;
            top: -25px;
            left: 0;
            background: #0B266C;
            backdrop-filter: blur(20px);
            width: 100%;
            max-width: 390px;   
        }

        h2 {
            color: $white;
        }
    }

    &__bottom {
        display: flex;
        justify-content: space-between;

        &-title {
            font-weight: 500;
            font-size: 30px;
            line-height: 115%;
            letter-spacing: 0.5px;

            color: #0B266C;
            margin-bottom: 37px;
        }

        &-item {
            width: 46%;
        }
    }
}

@include below(1281px) {
    .agreements {
        &__top {
            &-title {
                top: -50px;
                left: -100px;
            }
        }
    }
}

@include below(1124px) {
    .agreements {
        &__top {
            padding-left: 0;
            &-title {
                position: relative;
                background: transparent;
                height: auto;
                backdrop-filter: none;
                padding: 0;
                top: auto;
                left: auto;
                text-align: center;
                margin-bottom: 30px;
                max-width: 100%;
            }

            h2 {
                color: #0B266C;
                br {
                    display: none;
                }
            }
        }
    }
}

@include below(769px) {
    .agreements {
        padding: 25px 0;

        &__container {
            padding: 0;
        }

        &__top {
            padding-bottom: 25px;
        }

        &__bottom {
            padding: 0 20px;
            flex-direction: column;

            &-item {
                width: 100%;

                &:first-child {
                    margin-bottom: 24px;
                }
            }

            &-title {
                font-size: 19px;
                line-height: 115%;
                margin-bottom: 27px;
            }
        }
    }
}