@import "main-bx";

*, *::after, *::before {
    box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, ul, p {
    margin: 0;
    padding: 0;
}



body {
    position: relative;
    font-family: $rubik_font;
    font-size: 16px;
    background-color: $white;
    width: 100%;
    max-width: $body_width;
    margin: 0 auto;
    color: $black;
    overflow-x: hidden;
}

.main {
    position: relative;
    z-index: 100;
} 

.container {
    width: 100%;
    max-width: map-get($container, xl);
    margin: 0 auto;

    @include below(1281px) {
        max-width: map-get($container, lg);
    }

    @include below(map-get($breaks, lg)) {
        max-width: map-get($container, mlg);
        padding: 0 20px;
    }
}

ul {
    list-style-type: none;
}

a {
    text-decoration: none;
}

:focus {
    outline-style: none;
    outline-width: 0 !important;
    outline-color: none !important;
}

input,
button,
textarea {
    background: transparent;
    border: none;
}

img {
    width: 100%;
    max-width: 100%;
    height: 100%;
    object-fit: cover;
}


.mfp-content {
    vertical-align: top !important;
    position: static !important;
}

.mfp-close {
    display: none !important;
}

.mfp-bg,
body.mfp-zoom-out-cur {
    overflow: hidden !important;
    margin: 0 !important;
    padding: 0 !important;
    height: 100% !important;
}

.mfp-wrap {
    overflow: auto;
    background: rgba(26,52,122, 0.8);
    backdrop-filter: blur(24px);
}