@import "main-bx";

.graph__slider {
    height: 100%;
    .slick-list {
        height: 100%;
    }

    &-nav {
        @include below(769px) {
            display: none;
        }
        &_mobile {
            position: absolute;
            display: none;
            width: 100%;
            @include below(769px) {
                display: block;
                padding: 0 20px;
                top: 60%;
            }

            button {
                width: 60px;
                height: 33px;
                border-right: none;
                display: flex;
                justify-content: center;
                align-items: center;
                background: #2838E9;
                border-radius: 5px;

                @include below(370px) {
                    width: 50px;
                    height: 25px;
                    padding: 0 15px;
                }
            }

            .slider-button__prev {
                transform: rotate(-180deg)
            }

            button svg {
                width: 26.14px;
                height: 6.88px;
                fill: #ffffff;
            }

            .slider-button {
                max-width: 100%;
                display: flex;
                justify-content: space-between;
                background-color: transparent;
            }
        }
    }
    &-item {
        position: relative;
        &:not(:first-child) {
            display: none;
        }
    }
    &-inner {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        z-index: 100;

        @media (max-width: 769px) {
            top: auto;
            bottom: -70px;
        }
    }

    &-top {
        display: flex;
        align-items: center;
        margin-bottom: 22px;

        @include below(769px) {
            margin-bottom: 0;
        }
    }

    &-date {
        font-weight: 500;
        font-size: 25px;
        line-height: 40px;
        margin-right: 16px;

        @include below(769px) {
            font-weight: 500;
            font-size: 16px;
            line-height: 40px;
        }
    }

    &-month {
        color: $blue;
    }

    &-year {
        color: $black;
    }

    &-popup {
        position: absolute;
        top: 60px;
        left: 0;
        background: #FFFFFF;
        backdrop-filter: blur(20px);
        border-radius: 15px;

        &_six {
            left: 30px;
        }
        
        &_right {
            left: auto;
            right: 0;
        }

        &_eight {
            right: 170px;
        }


        @include below(769px) {
            background: transparent;
            backdrop-filter: none;
            border-radius: 0;
            width: 100%;
            height: 100%;
            position: relative;
            top: auto;
            left: auto;
            right: auto;
        }

        &-inner {
            position: relative;
            padding: 25px 35px;

            @include below(769px) {
                background: #F5F8FB;
                backdrop-filter: blur(20px);
                border-radius: 15px;
                padding: 14px 14px 24px 24px;
            }

            &::before {
                content: "";
                position: absolute;
                top: -12px;
                left: 0;
                width: 0;
                height: 0;
                border-style: solid;
                border-width: 0 21px 21px 21px;
                border-color: transparent transparent #ffffff transparent;

                @include below(1024px) {
                    content: none;
                }
            }

            &.figure-1::before {
                left: 27px;
                @include below(1281px) {
                    left: 20px;
                }
            }

            &.figure-2::before {
                left: 127px;
                @include below(1281px) {
                    left: 110px;
                }
            }

            &.figure-3::before {
                left: 228px;

                @include below(1281px) {
                    left: 195px;
                }
            }

            &.figure-4::before {
                left: 328px;
                @include below(1281px) {
                    left: 280px;
                }
            }

            &.figure-5::before {
                left: 430px;
                @include below(1281px) {
                    left: 365px;
                }
            }

            &.figure-6::before {
                left: 500px;
                @include below(1281px) {
                    left: 420px;
                }
            }

            &.figure-7::before {
                left: 60px;
                @include below(1281px) {
                    left: 150px;
                }
            }

            &.figure-8::before {
                left: 80px;
                @include below(1281px) {
                    left: 150px;
                }
            }

            &.figure-9::before {
                left: 110px;
                @include below(1281px) {
                    left: auto;
                    right: 270px;
                }
            }

            &.figure-10::before {
                left: 130px;
                @include below(1281px) {
                    left: auto;
                    right: 190px;
                }
            }

            &.figure-11::before {
                left: auto;
                right: 130px;
                @include below(1281px) {
                    left: auto;
                    right: 100px;
                }
            }

            &.figure-12::before {
                left: auto;
                right: 30px;
                @include below(1281px) {
                    left: auto;
                    right: 10px;
                }
            }
        }
    }

    &-text {
        br {
            @media (max-width: 769px) {
                display: none;
            }
            
        }
    }
}