@import "main-bx";

.constructions {
    padding: 62px 0 40px;
    @include below(769px) {
        padding: 25px 0;
    }

    &__carousel {
        border-radius: 5px;
        overflow: hidden;
    }

    &__out {
        @include below(769px) {
            padding: 0 20px;
        }
    }

    &__inner {
        position: relative;

        @include below(769px) {
            padding: 0;
        }
    }

    &__title {
        text-align: center;
        margin-bottom: 60px;

        @include below(769px) {
            margin-bottom: 25px;
            padding: 0 20px;
        }
    }

    &__nav {
        top: 670px;

        @include below(769px) {
            display: flex;
            width: 100%;
            padding: 0 20px;

            top: 280px;
            left: 0;
        }
    }
}