@import "main-bx";

.slider-button {
    position: sticky;
    z-index: 10;
    width: 100%;
    max-width: 100px;
    padding: 8px 7px;
    border-radius: 45px;
    background-color: $smoke_blue;
    .slick-disabled {
        pointer-events: none;
        svg {
            opacity: 0.3;
        }
    }

    &__prev {
        cursor: pointer;
        padding: 4px 7px;
        border-right: 1.5px solid rgba(60, 76, 253, .3);
        svg {
            width: 19px;
            height: 9px;
            fill: $blue3;
            transition: all .2s linear;
        }
    }
    
    &__next {
        cursor: pointer;
        padding: 4px 7px;
        
        svg {
            width: 29px;
            height: 9px;
            fill: $blue3;
            transition: all .2s linear;
        }
    }
}


.slider-nav {
    position: absolute;
    top: 45%;
    left: -5%;
    z-index: 1;
    width: 110%;
    display: flex;
    justify-content: space-between;
    @include below(1130px) {
        display: none;
    }


    @include below(769px) {
        svg {
            width: 26.14px;
            height: 6.88px;
            fill: $white;
        }

        button {
            width: 60px;
            height: 33px;
            background: #2838E9;
            border-radius: 5px;

            &.slick-disabled {
                svg {
                    opacity: 0.6;
                }
            }
        }

        .slider-nav__prev {
            transform: rotate(-180deg);
        }
    }

    &_des {
        @include below(769px) {
            display: none;
        }
    }
    &_mob {
        display: none;
        
        @include below(769px) {
            display: flex;
            align-items: center;
        }
        
        
    }

    svg {
        width: 18px;
        height: 60px;
    }

    button {
        cursor: pointer;
        padding: 0 20px;

        @include below(370px) {
            width: 50px;
            height: 25px;
            padding: 0 15px;
        }
    }
}