@import "main-bx";

.header {
	position: absolute;
    top: 50px;
    left: 0;
    right: 0;
    z-index: 1000;
    transition: all .2s linear;

    &_second {
        top: 0;
        background: rgba(12, 40, 114, 0.9);
        backdrop-filter: blur(24px);
    }

    &__burger {
        display: none;
        padding: 0 15px;

        img {
            width: 25px;
            height: 25px;
        }
    }
    &.fixed {
        position: fixed;
        background: rgba(12, 40, 114, 0.9);
        backdrop-filter: blur(24px);
        transform: translateY(-50px);
    }

    &.show {
        background: rgba(12, 40, 114, 0.9);
        backdrop-filter: blur(24px);
        .header__wrap::before {
            opacity: 1;
            z-index: 1;
            height: 500px;
        }
    }

    &__logo {
        img {
            width: 100%;
            max-width: 117px;
            height: 27px;
        }
    }

    &__wrap {
        padding: 30px 0;
        position: relative;
        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            width: 100%;
            height: 0;
            z-index: -1;
            background: rgba(12, 40, 114, 0.9);
            backdrop-filter: blur(24px);
            opacity: 0;
            transition: all .2s linear;
        }
    }

    &__inner {
        position: sticky;
        z-index: 100;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
    }

    &__big-logo {
        display: none;

        position: absolute;
        bottom: 0;
        right: 20px;

    }

    &__mobile {
        display: none;
    }
}

@include below(1040px) {
    .header {
 
        &__inner {
            align-items: center;
        }
        &__wrap {
            padding: 20px 0;
            &::before {
                content: none;
            }
        }

        &__menu {
            display: none;
        }

        &__big-logo {
            display: block;
        }

        &__burger {
            display: block;
        }

        &__mobile {
            display: flex;
            justify-content: space-between;
            background: #0C2872;
            backdrop-filter: blur(24px);
            padding: 20px 0 20px 20px;
            align-items: center;
        }
    }
}

@include below(1040px) {
    .header {
        top: 20px;
        &.fixed {
            top: 50px;
        }

        &_second {
            top: 0;
        }
    }
}

.mfp-content .header__menu {
    display: block;
}