@import "main-bx";

.graph {
    padding-top: 42px;
    margin-bottom: 43px;
    background-color: $smoke_blue;
    &__top {
        position: relative;
         
        &::before {
            content: "";
            position: absolute;
            bottom: 18px;
            left: 0%;
            z-index: 1;
            width: 100%;
            height: 5px;
            background: #BCD6EF;
        }
    }

    &__bottom {
        position: relative;
        margin-top: -20px;
    }

    &__slider {
        .slick-list {
            height: 100% !important;
        }
    }

    &__img {
        position: sticky;
        z-index: 10;
        // margin-bottom: -20px;
        max-height: 100rem;
    }

    &__title {
        text-align: center;
        margin-bottom: 55px;
    }
}

@include below(769px) {
    .graph {
        border-radius: 50px 0px 0px 0px;
        margin-bottom: 70px;
        &__title {
            margin-bottom: 26px;
        }

        &__slider {
            .slick-list {
                overflow: visible !important;
            }
        }
        &__slick {
            
        }
        &__img {
            height: 300px;
        }
    }
}