@import "main-bx";

.constructions__solutions {
    padding: 32px 60px 40px;
    background-color: #F5F8FB;
    border-radius: 0 0 5px 5px; 
    
    @include below(769px) {
        border-radius: 0 0 15px 15px;
        padding: 30px 20px 0;
    }

    &-title {
        font-weight: 500;
        font-size: 20px;
        line-height: 115%;
        letter-spacing: 0.5px;

        color: #2F323D;
        margin-bottom: 25px;

        @include below(769px) {
            font-size: 16px;
            line-height: 115%;
        }
    }

    &-list {
        display: flex;
        justify-content: space-between;
    }

    &-item {
        display: flex;
        &:not(:last-child) {
            margin-right: 45px;

            @include below(769px) {
                margin-right: 0;
            }
        }

        @include below(769px) {
            width: 48%;
        }

        svg {
            max-width: 19.41px;
            height: 22px;
            fill: $blue3;
            margin-right: 14px;

            @include below(769px) {
                max-width: 12.15px;
                height: 13.77px;
                fill: #ADC1D5;
                margin-right: 7px;
            }
        }

        p {
            font-weight: normal;
            font-size: 14px;
            line-height: 150%;

            color: #0D223D;

            @include below(769px) {
                font-size: 10px;
                line-height: 140%;
                margin-top: -5px;
            }
        }
    }
}


@include below(map-get($breaks, lg)) {
    .constructions__solutions {
        &-list {
            flex-wrap: wrap;
        }

        &-item {
            width: 40%;
            margin-bottom: 20px;
        }
    }
}