@import "main-bx";

.not-found {
    text-align: center;
    padding-top: 240px;

    @include below(769px) {
        padding-top: 130px;
    }

    &__logo {
        margin-bottom: 50px;
        img {
            width: 70px;
            height: 80px;
        }
    }

    &__title {
        margin-bottom: 22px;
    }

    &__text {
        font-size: 18px;
        line-height: 145%;
        text-align: center;
        letter-spacing: 0.005em;

        color: #2F323D;
    
        margin-bottom: 60px;

        @include below(460px) {
           br {
               display: none;
           }
        }
    }
    
    &__link {
        a {
            display: block;
            width: 300px;
            padding: 20px;
            margin: 0 auto;

            @include below(460px) {
                padding: 15px 20px;
            }
        }
    }
}