@import "main-bx";

.footer {
    padding: 20px 0;
    background-color: $blue3;
    &__row {
        display: flex;
        justify-content: space-between;
        align-items: center;

        @include below(769px) {
            align-items: baseline;
        }
    }


    &__item {
        display: flex;
        align-items: center;
        @include below(769px) {
            width: 50%;
            flex-direction: column;
            align-items: baseline;
        }
    }

    &__logo {
        width: 160px;
        height: 34px;
        margin-right: 45px;

        @include below(769px) {
            margin-right: 0;

            width: 82.22px;
            height: 17.47px;
            margin-bottom: 20px;
        }

        &_second {
            width: 75px;
            height: 22px;
            margin-right: 0;
            margin-left: 94px;

            @include below(769px) {
                margin-left: auto;

                width: 55px;
                height: 16px;
            }

            .footer__logo {
                margin-right: 0;
            }
        }
    }

    &__copy {
        font-size: 14px;
        line-height: 20px;

        color: #E8ECF1;

        @include below(769px) {
            font-size: 10px;
            line-height: 16px;
            white-space: nowrap;
        }
    }

    &__socials {
        display: flex;

        @include below(769px)  {
            display: none;
        }
    }
}