@import "main-bx";

.replacement {
    padding: 65px 0 100px;

    &__inner {
        position: relative;
    }

    &__nav {
        top: 250px;
    }

    &__title {
        font-weight: bold;
        font-size: 35px;
        line-height: 115%;

        letter-spacing: 0.5px;

        color: #0C2872;
        margin-bottom: 40px;
    }

    &__slider {

        &-item {
            padding: 70px 90px;
            border-radius: 5px;
            background-color: $smoke_blue;
            &:not(:first-child) {
                display: none;
            }
        }
        &-inner {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }


        .slick-track {
            display: flex;
        }
        
    }

    &__img {
        
    }

    &__text {
        max-width: 670px;
        font-size: 17px;
        line-height: 155%;
    
        p:nth-child(2) {
            margin-bottom: 30px;
        }
    }
}


@include below(1281px) {
    .replacement {
        &__img {
            max-width: 240px;
        }

        &__text {
            max-width: 450px;
        }
    }
}

@include below(map-get($breaks, mlg)) {
    .replacement {
        &__slider {
            &-inner {
                flex-direction: column;
                align-items: center;
            }
        }

        &__img {
            margin-right: 0;
            margin-bottom: 30px;
        }
    
        &__text {
            max-width: 100%;
        }
    }
}


@include below(769px) {
    .replacement {
        padding: 150px 0 0;

        &__nav {
            display: flex;
            width: 100%;
            top: -12px;
            left: 0;
            padding: 0 20px;
        }

        &__inner {
            padding: 0;
        }
        &__title {
            text-align: center;
            margin-bottom: 0;
            font-size: 25px;
            position: absolute;
            top: -150px;
            left: 0;
            right: 0;
            padding: 0 20px;
        }

        &__text {
            font-size: 12px;
            line-height: 140%;

            p:first-child {
                margin-bottom: 15px;
            }
        }

        &__slider {
            .slick-list {
                overflow: visible !important;
            }
            &-item {
                padding: 0 20px 40px;
            }
        }

        &__img {
            max-width: 145px;
            margin-top: -80px;
        }
    }
}