@import "main-bx";

.constructions__slider {
    
    &-img {
        @include below(769px) {
            height: 300px;
        }
    }

    &-item {
        height: 100%;
        &:not(:first-child) {
            display: none;
        }

        &.slick-slide {
            height: auto;
        }

    }

    &-bottom {
        padding: 50px 0 0;
        background-color: $smoke_blue;
        @include below(769px) {
            padding: 25px 0 0;
            margin-top: -25px;
            position: sticky;
            z-index: 100;
            border-radius: 15px;
            overflow: hidden;
        }
    }

    &-object {
        position: sticky;
        z-index: 100;
        display: flex;
        justify-content: space-between;
        padding: 35px 50px 35px 70px;
        background-color: $smoke_blue;
        border-radius: 5px;
        width: 100%;
        max-width: 1075px;
        margin: 0 auto;
        margin-top: -90px;

        @include below(769px) {
            flex-direction: column;
            margin-top: 0;
            padding: 0 30px 15px 20px;
        }
    }

    &-title {
        width: 40%;
        font-weight: 500;
        font-size: 35px;
        line-height: 115%;
        letter-spacing: 0.5px;
        margin-right: 25px;

        @include below(769px) {
            width: 100%;
            font-size: 16px;
            line-height: 115%;
            margin-bottom: 12px;
            margin-right: 0;
        }
    }

    &-text {
        width: 60%;
        line-height: 170%;
        color: #0D223D;

        span {
            &:first-child {
                display: block;
                margin-bottom: 20px;
            }
        }

        @include below(769px) {
            font-size: 12px;
            line-height: 140%;
            width: 100%;
        }
    }
}

